/**
 * Strictly Layout Styles
 */
@media (min-width: 768px) {
  .site-header {
    padding: 1rem;
  }
  .site-header .site-branding {
    display: block;
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
  // logo
  .site-header .site-branding img {
    height: auto;
    max-width: 230px;
    max-height: none;
  }

  .content-area {
    //
  }

  .widget-area {
    //
  }

  .wpcotestheme-full-width-content .site-content,
  .page-template-fullwidth .site-content,
  .page-template-clean-fullwidth .site-content {
    display: block;
    width: 100%;
  }

  .wpcotestheme-sidebar .site-content {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: 2rem;
    padding: 1rem;
  }

  @import "../components/comment";
  @import "primary-navigation";
  @import "secondary-navigation";
}

.header-hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
  object-position: center;
  width: 100%;
  min-height: 300px;
  margin-bottom: var(--space-xl);
}

.header-hero__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.header-hero__title {
  color: #fff;
}

.entry-title {
  text-align: center;
}
.homepage-description {
  padding: 1rem;
  text-align: center;
  font-size: 1.2rem;
}

@media (min-width: 1000px) {
  .site-main .grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
} // mediaquery

.archive-header,
.homepage-header {
  text-align: center;
}
.pagination {
  text-align: center;
}

.pagination .page-numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}
.nav-links li {
  margin-right: 1rem;
}

.archive .format-standard,
.blog .format-standard {
  display: block;
  // border: 1px solid;
  text-align: center;
  padding: 0 1rem 1rem;
}
// Front page
.custom-button-line-header-primary > a {
  border: var(--border-button) solid var(--color-primary);
  background-color: transparent !important;
}
.custom-button-line-header-secondary > a {
  border: var(--border-button) solid var(--color-secondary);
  background-color: transparent !important;
}
.home .header-title {
  margin: var(--space-sm) 0;
}

